import React, { Component } from 'react';
import Pictureattachments from './Pictureattachments';
import ContentEditable from 'react-contenteditable';

class Editorpanel extends Component {
	constructor(props) {
		super(props);
		this.handleChange = this.handleChange.bind(this);
		this.state = {
			html: 'k'
		};
	}
	componentDidMount() {
		let content = this.props.textbody;
		this.setState({
			html: this.props.textbody
		});
	}
	handleChange(e) {
		this.setState({ html: e.target.value });
		console.log(this.state.html);
	}
	render() {
		console.log(this.props.gettitle());
		return (
			<div id="right-panel">
				<div id="drag" />
				<h3 className="title">{this.props.date}</h3>
				<Pictureattachments haspictures={this.props.haspictures} />

				<ContentEditable
					id="editor"
					className="editor"
					innerRef={this.contentEditable}
					html={this.state.html} // innerHTML of the editable div
					disabled={false} // use true to disable editing
					onChange={this.handleChange} // handle innerHTML change
					tagName="div" // Use a custom HTML tag (uses a div by default)
				/>
			</div>
		);
	}
}
export default Editorpanel;
