import React, { Component } from 'react';
import Locknote from './components/Locknote';
import Toolbar from './components/Toolbar';
import Notespanel from './components/Notespanel';
import Editorpanel from './components/Editorpanel';
import uuid from 'uuid/v4';

import Dragging from './components/Functions';
import './components/App.css';

class App extends Component {
	constructor(props) {
		super(props);
		this.handleLockClicked = this.handleLockClicked.bind(this);
		this.handleNoteSelected = this.handleNoteSelected.bind(this);
		this.handleAddNote = this.handleAddNote.bind(this);
		this.handleDeleteNote = this.handleDeleteNote.bind(this);
		this.handleGetTitle = this.handleGetTitle.bind(this);
		this.handleGetContent = this.handleGetContent.bind(this);

		this.setNewDate = this.setNewDate.bind(this);

		//-------- Temporary state
		this.state = {
			selectednoteid: '',
			title: '',
			body: 'kkt',
			date: '',
			locked: false,
			haspictures: false
		};

		this.state = {
			notes: [
				{
					id: uuid(),
					title: 'New note',
					body: 'This is a test body. Hello world. It comes from the state',
					date: this.setNewDate(),
					locked: false,
					selected: false,
					haspictures: false,
					picturepath: './pics/'
				}
			]
		};
	}
	// -------------- Handlers

	setNewDate() {
		let d = new Date();
		let year = d.getFullYear();
		let month = d.getMonth() < 10 ? '0' + (d.getMonth() + 1) : d.getMonth() + 1;
		let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate();
		let fullDate = `${year}-${month}-${day}`;
		return fullDate;
	}
	//--------- Lifecycles ----
	//--------- Called before the render of all components
	componentWillMount() {
		this.setState({
			selectednoteid: this.state.notes[0].id,
			body: this.state.notes[0].body,
			date: this.state.notes[0].date,
			locked: this.state.notes[0].locked,
			haspictures: this.state.notes[0].haspictures
		});
	}
	//-------- Called after rendering of all components
	componentDidMount() {
		Dragging();
	}

	handleLockClicked() {
		this.setState({ locked: (this.locked = !this.locked) });
	}

	handleNoteSelected(e) {
		this.setState({ selectednoteid: e });
	}

	handleAddNote() {
		const newNote = {
			id: uuid(),
			title: 'New note',
			body: 'This is a test body. Hello world',
			date: this.setNewDate(),
			locked: false,
			selected: false,
			haspictures: false,
			picturepath: './pics/'
		};

		this.setState({ notes: [...this.state.notes, newNote] });
	}

	handleDeleteNote() {
		console.log('delete pressed for id', this.state.selectednoteid);
		this.setState({
			notes: [
				...this.state.notes.filter(
					note => note.id !== this.state.selectednoteid
				)
			]
		});
	}
	handleGetTitle() {
		let getIndex = this.state.notes.findIndex(el => {
			return el.id === this.state.selectednoteid;
		});

		let getBody = this.state.body;
		let titleSubstring = getBody.substring(0, 19) + '...';
		return titleSubstring;
	}

	handleGetContent() {
		let getIndex = this.state.notes.findIndex(el => {
			return el.id === this.state.selectednoteid;
		});

		return this.state.body;
	}

	handleWriteContent(e) {
		let getIndex = this.state.notes.findIndex(el => {
			return el.id === this.state.selectednoteid;
		});
	}
	//-------------

	render() {
		return (
			<div className="App" id="container">
				<Locknote
					editorlock={this.handleLockClicked}
					caneditnote={this.state.caneditnote}
				/>

				<Toolbar
					addnote={this.handleAddNote}
					deletenote={this.handleDeleteNote}
				/>
				<Notespanel
					notes={this.state.notes}
					noteselected={this.handleNoteSelected}
					selectednoteid={this.state.selectednoteid}
				/>
				<Editorpanel
					gettitle={this.handleGetTitle}
					textbody={this.state.body}
					date={this.state.date}
					onChange={this.state.body}
					canedit={this.state.caneditnote}
					haspictures={this.state.haspictures}
				/>
			</div>
		);
	}
}

export default App;
